export function parseTime(time){
   let hour;
   let min;
   console.log("running parseTime")
   switch(time.length){
      case 3: //1-9AM,1-9PM
         console.log("entered Case 3")
         if(time.includes("am")){
            hour = time.charAt(0)
            min = 0
            break
         }
         else if(time.includes("pm")) {
            hour = parseInt(time.charAt(0))+12
            min = 0
            break
         }
      case 4: //0:00 - 9:00, 12PM, 12AM
         if(time.includes("am")){
            hour = parseInt(12)
            min = 0
            break
         }
         else if(time.includes("pm")){
            hour = parseInt(12)+12
            min = 0
            break
         }
         else{
            hour = parseInt(time.charAt(0))
            min = parseInt(time.slice(2,4))
            break
         }
      case 5: //00:00 - 24:00
         hour = parseInt(time.slice(0,2))
         min = parseInt(time.slice(2,4))
         break
      case 6: //1:00AM/PM - 9:00AM/PM
         if(time.includes("am")){
            hour = parseInt(time.charAt(0))
            min = parseInt(time.slice(2,4))
            break
         }
         else{
            hour = parseInt(time.charAt(0))+12
            min = parseInt(time.slice(2,4))
            break
         }
      case 7: //1:00 AM/PM - 9:00 AM/PM
         if(time.includes("am")){
            hour = parseInt(time.charAt(0))
            min = parseInt(time.slice(2,4))
            break
         }
         else{
            hour = parseInt(time.charAt(0))+12
            min = time.slice(2,4)
            break
         }
      case 8: //10:00 AM/PM - 12:00 AM/PM
         if(time.includes("am")){
            hour = time.slice(0,2)
            min = time.slice(2,4)
            break
         }
         else{
            hour = parseInt(time.slice(0,2))+12
            min = time.slice(2,4)
            break
         }
      case 10: //1:00:00 AM/PM - 9:00:00 AM/PM
         if(time.includes("am")){
            hour = parseInt(time.charAt(0))
            min = time.slice(2,4)
            break
         }
         else{
            hour = parseInt(time.charAt(0))+12
            min = time.slice(2,4)
            break
         }
      case 11: //10:00:00 AM/PM - 12:00:00 AM/PM
         if(time.includes("am")){
            hour = time.slice(0,2)
            min = time.slice(2,4)
            break
         }
         else{
            hour = parseInt(time.slice(0,2))+12
            min = time.slice(2,4)
            break
         }
      default:
         hour = 0
         min = 0
         break
   
      }
   
   hour = parseInt(hour)
   min = parseInt(min)
   console.log("end of parseInt")
   console.log(hour,min)
   return {hour,min};
   }

export function formatDate(date){
   let tempDay;
   if(date.includes("-")){
      date=date.split("-")
      tempDay = date[0]
      date[0] = date[1]
      date[1] = tempDay
      date=date.join("-")
   }
   if(date.includes("/")){
      date=date.split("/")
      tempDay = date[0]
      date[0] = date[1]
      date[1] = tempDay
      date=date.join("/")
   }
   return date
}